.container {
    color: var(--primary);
    background: var(--secondary);
    cursor: default;
}

.section {
    min-height: 100vh;
    padding-top: 0px;
    padding-left: 40px;
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.s2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 50%;
}

.s1 {
    padding: 160px 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    max-width: 50%;
}

.section::before {
    content: "";
    background-image: url("../images/chicago2.jpeg");
    background-size: cover;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: 0.3;
}

.s1Title {
    font-size: 48px;
    font-family: "Montserrat SemiBold";
    font-weight: 600;
    z-index: 1;
}

.s1Subtitle {
    font-size: 20px;
    margin: 18px 0;
    font-family: "Montserrat";
    font-weight: 400;
    z-index: 1;
}

.s1Link {
    margin-top: 10px;
    position: relative;
    border: none;
    background: white;
    color: var(--secondary);
    padding: 16px 40px;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.9rem;
    font-family: "Montserrat SemiBold";
    font-weight: 600;
    border-radius: 10px;
}

.s1Link:hover:before {
    transform: translate3d(-5px, 5px, 0);
}

.s1Link:before {
    border: 1px solid #fff;
    box-sizing: border-box;
    content: " ";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transform: translateZ(0);
    transition: transform 0.2s;
    transition-property: transform;
    transition-duration: 0.2s;
    transition-timing-function: ease;
    transition-delay: 0s;
    width: 100%;
    border-radius: 10px;
}

.s2Image {
    width: 100%;
    z-index: 1;
}

@media (max-width: 780px) {
    .section {
        flex-direction: column;
        padding-left: 0px;
        width: 100%;
        padding-top: 80px;
    }
    .s1 {
        padding: 100px 45px 0px 45px;
        max-width: 100%;
    }
    .s1Title {
        font-size: 30px;
        z-index: 1;
    }
    .s1Subtitle {
        font-size: 17px;
        z-index: 1;
    }
    .section::before {
        content: "";
        background-image: url("../images/chicago2.jpeg");
        background-size: cover;
        position: absolute;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        opacity: 0.3;
    }
    .s2Image {
        width: 350px;
        z-index: 1;
    }
    .s2 {
        width: 100%;
        padding: 15px;
        max-width: 100%;
    }
}